.school {
  position: relative;
  float: left;
  width: 100%;
  background-color: #fcfaeb;

  .container {
    text-align: center;
  }

  &__title {
    display: inline-block;
    position: relative;
    margin-top: 50px;
    margin-bottom: 30px;
    padding-bottom: 5px;
    @include box-sizing(border-box);
    text-align: center;
    border-bottom: 1px solid #4a5b8f;
    font-family: $ProximaNovaLight;
    font-size: 22px;
    color: #4a5b8f;

    b {
      padding-left: 6px;
      font-family: $ProximaNovaBold;
      color: #938c4e;
    }

  }

  &__wrapper {
    @include display-flex(flex);
    @include flex-wrap(wrap);
    @include flex-direction(row);
    @include justify-content(space-around);
    position: relative;
    float: left;
    width: 100%;
    margin-bottom: 70px;

    &--item {
      position: relative;
      float: left;
      width: calc(25% - 25px);
      width: -moz-calc(25% - 25px);

      .photo {
        position: relative;
        float: left;
        width: 100%;
        height: 190px;
        overflow: hidden;

        img, svg {
          size: 100%;
          object-fit: cover;
        }

      }

      .title{
        position: relative;
        float: left;
        width: 100%;
        font-size: 26px;
        font-family: $ProximaNovaLight;
        text-align: justify;
        color: #4a5b8f;
        margin-top: 20px;
        margin-bottom: 0;

        b {
          padding-left: 6px;
          font-family: $ProximaNovaBold;
        }

      }

      .date{
        position: relative;
        float: left;
        width: 100%;
        margin-top: 0;
        margin-bottom: 15px;
        text-align: justify;
        font-size: 18px;
        color: #aba266;
      }

      .text{
        display: block;
        display: -webkit-box;
        position: relative;
        float: left;
        width: 100%;
        margin-top: 0;
        margin-bottom: 15px;
        text-align: justify;
        font-size: 16px;
        color: #3e3c3f;
        line-height: 1.2;
        height: calc(16px * 1.2 * 8);
        -webkit-line-clamp: 8;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      
      .more{
        display: block;
        position: absolute;
        right: 0;
        bottom: -5px;
        padding: 10px;
        background-color: #fff;
        text-align: center;
        z-index: 1;
        
        span {
          background-color: #78a1c0;
          padding: 5px 22px;
          font-size: 18px;
          font-style: italic;
          color: #fff;

          &:after {
            display: block;
            position: absolute;
            content: '';
            right: 0;
            top: 5px;
            width: 0;
            height: 0;
            border-top: 16px solid transparent;
            border-left: 11px solid #78a1c0;
            border-bottom: 16px solid transparent;
          }
        }
      }
    }
  }
}