$Generic: 'Generic';
@font-face {
  font-family: 'Generic';
  src: url('../fonts/Generic/GenericFont.eot') format('embedded-opentype'),
  url('../fonts/Generic/GenericFont.woff2') format('woff2'),
  url('../fonts/Generic/GenericFont.woff') format('woff'),
  url('../fonts/Generic/GenericFont.ttf') format('truetype'),
  url('../fonts/Generic/GenericFont.svg') format('svg');
  font-weight: normal;
}
