.header {
  position: relative;
  float: left;
  width: 100%;
  z-index: 1;

  &__logo {
    position: relative;
    float: left;
    width: 125px;
    height: 140px;
    overflow: hidden;
    margin-top: 15px;

    img, svg {
      size: 100%;
      object-fit: contain;
    }

  }

}